<template>
<AppHeader />

<div class="w-full flex">
 <div class="m-auto">
  <h1 class="text-3xl text-center">Bojack Horseman Characters {{characterCount}}</h1>
  <ul>
   <li class="flex text-2xl justify-between" v-for="(character, index) in characters" :key="character.name">
    <div> {{ character.name }} </div>

    <button @click="removeCharacter(index)"> x </button>
   </li>
  </ul>

  <form class="mt-4 text-2xl" @submit.prevent="addCharacter">

   <input class="border rounded border-pink-800" v-model="newCharacter" placeholder="type here" />
   <button class="border rounded border-pink-800" type="submit">Add a new character</button>

  </form>
  <small class="italic"> total count is: {{characterCount}} </small>
 </div>
</div>
<AppFooter />
</template>

<script>
import AppHeader from './components/AppHeader';
import AppFooter from './components/AppFooter';

export default {
 components: {
  AppHeader,
  AppFooter
 },
 computed: {
  characterCount() {
   return this.characters.length;
  },
 },
 methods: {

  addCharacter() {
   if (this.newCharacter !== '') {
    this.characters.push({
     name: this.newCharacter
    });
    this.newCharacter = "";
   }
  },
  removeCharacter(index) {
   this.characters = this.characters.filter((character, i) => i !== index);
  },
 },
 data() {
  return {
   attribute: 'value',
   isDisabled: false,
   newCharacter: 'Name',
   characters: [{
     name: 'Bojack',
    },
    {
     name: 'Diane',
    },
    {
     name: 'Mr. Peanutbutter',
    },
    {
     name: 'Princess Carolyn',
    },
    {
     name: 'Todd Chavez',
    },
    {
     name: 'theDevil',
    },
   ],

  }
 },

}
</script>

<style></style>
